import React, { Component } from 'react'

export default class AboutUs extends Component {
    render() {
        return (
            <div className="">
                <nav className="pa2 pa2-ns">
                    <h1
                        className=" black b f1 tc db mb2 mb4-ns"
                    >
                        About Us
                    </h1>
                </nav>

                <article className="center mw7 pa2 ma2 mw8-ns br3 hidden ba b--black-10 mv4 shadow-2 ">
                                    
                

                <div className="">

                <article className="cf">
                    <div className=" ph3 ph5-ns fl w-100 w-100-ns tj bg-light-grey">
                    <button style={{
                        fontWeight: "bold",
                        color: "white", 
                        border: '6px solid black',
                        borderRadius: '4px', 
                        backgroundColor:'black'}}>OUR HISTORY</button>
                        <p className="f6 f5-l lh-copy tj">
                        The mission to the unreached areas church vocational training center (MUAC-VTC) is a 
                        private vocational training institution owned and managed by the Mission to the unreached areas Church. 
                        It was established in 2015 under the church which is led by Bishop Samwel Meena  and registered by 
                        vocational Education and training Authority (VETA) with registration Number VET/PWN/PR/2015/C/045. 
                        It is located at Mlandizi opposite to Ruvu JKT primary school in Kibaha, Coastal region
                        </p>
                        <p className="f6 f5-l lh-copy tj">
                        It was a dream come true that was in the heart of Bishop Samwel Meena for more than 20 years to help people especially 
                        those from rural areas to get proper vocational education. He started it with his friend and co-founder Mr. Sassi who 
                        helped by building the classrooms and providing machines and other teaching tools that are used until now.
                        </p>
                        <p className="f6 f5-l lh-copy tj">
                        The training school started with 17 students from different villages in Tanzania who come from poor and under-privileged 
                        families that can’t afford paying the tuition fee who received education freely for 2 years where at the end of their studies 
                        3 students got employed by the school and 5 students managed to start their own businesses.
                        </p>
                        <p className="f6 f5-l lh-copy tj">
                        These students were initially enrolled in three programmes, namely - Design sewing and cloth technology (DSCT), Domestic Electrical Installation (DEI) 
                        and Carpentry and Joinery (CJ).The range of Programmes has grown since its establishment, In 2017  MUAC-VTC introduced Information and communication technology (ICT) programme 
                        and also it is planning to introduce other programmes namely Plumbing, Masonry and Auto Electrical.
                        </p>
                        <p className="f6 f5-l lh-copy tj">
                        The MUAC-VTC is accepting and registering students with qualifications of certificate of secondary education (form four) and above.
                        Currently the MUAC – VTC has 5 programmes offered;
                        </p>
                        <ol className="f6 f5-l lh-copy tj">
                            <li>Design sewing and cloth technology</li>
                            <li>Domestic electrical installation</li>
                            <li>Auto Electrical</li>
                            <li>Carpentry and joinery</li>
                            <li>Information and communication technology</li>
                        </ol>
                        <p className="f6 f5-l lh-copy tj">
                        The training school has long courses programs of 2 years and short courses programs of 6 months “Basic Practical” and one year “intensive Practical”
                        </p>
                        <p className="f6 f5-l lh-copy tj">
                        The MUAC-VTC owes its existence and operations to its mission which focuses on “Capacity Building by offering Quality Education”. 
                        It produces the trainees who are well-known for their discipline, knowledge, skill, loyalty and integrity. The training institution 
                        boasts of a team of professionals who are dedicating their time in the development of students. 
                        The Main goal of MUAC-VTC is to impart skills to its students which match with the career opportunities as supplied by the labor market both within and outside of Tanzania
                        </p>
                    </div>
                </article>

                <article className="cf">
                    <div className=" pv3 ph3 ph5-ns fl w-100 w-100-ns tj bg-light-grey">
                    <button style={{
                        fontWeight: "bold",
                        color: "white", 
                        border: '6px solid black',
                        borderRadius: '4px', 
                        backgroundColor:'black'}}>OUR VISION</button>
                        <p className="f6 f5-l lh-copy tj">
                        To provide quality vocational education to people especially those from poor and under-priviledged families which match with the career opportunities as 
                        supplied by the labor market both within and outside of Tanzania.
                        </p>
                    </div>
                </article>
                <article className="cf">
                    <div className=" pv3 ph3 ph5-ns fl w-100 w-100-ns tj bg-light-grey">
                    <button style={{
                        fontWeight: "bold",
                        color: "white", 
                        border: '6px solid black',
                        borderRadius: '4px', 
                        backgroundColor:'black'}}>OUR MISSION</button>
                        <p className="f6 f5-l lh-copy tj">
                        Capacity Building of People of Tanzania To meet the Emerging employment Challenges happening in the country, 
                        By imparting Quality vocational Education with Discipline which leads to Self – Enlightenment and Development of the Nation.
                        </p>
                    </div>
                </article>
                </div>
                </article>

            </div>
        )
    }
}
                                