import React, { Component } from 'react'
import './App.css';
import Navbar from './landingPage/navbar'
import Home from './landingPage/home'
import Footer from './landingPage/footer'
import Administration from './navigation/Administration'
import AboutUs from './navigation/AboutUs'
import Departments from './navigation/Departments'
import Downloads from './navigation/Downloads'
import Gallery from './navigation/Gallery'
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'


class App extends Component {
  render() {
    return (
      <div className="App">
        
          <Router>
            <Navbar />
            <Switch>
              <Route exact path='/' component={ Home }  />
              <Route exact path='/administration' component={ Administration }  />
              <Route exact path='/aboutUs'  component={ AboutUs }  />
              <Route exact path='/departments' component={ Departments } />
              <Route exact path='/downloads' component={ Downloads } />
              <Route exact path='/gallery' component={ Gallery } />
            </Switch>
            <Footer />
          </Router>
        
      </div>
    )
  }
}

export default App
