import React, { Component } from 'react'
import tachyons from 'tachyons'
import { NavLink } from 'react-router-dom';

class Navbar extends Component {
    render() {
        return (
            <div>
                <nav className="bg-white sticky db dt-l w-100 border-box pa3 ph5-l shadow-2">
                    <a
                        className="db dtc-l v-mid mid-gray link dim w-100 w-25-l tc tl-l mb2 mb0-l"
                    
                        title="Home"
                    >
                        <img
                        src={require("../images/logo.svg")}
                        className="dib w2 h2 br-100"
                        alt="Site Name"
                        />
                    </a>
                    <div className="db dtc-l v-mid w-100 w-75-l tc tr-l">
                        <NavLink exact to= {'/'} exact={true} activeClassName='is-active' activeStyle={{
                        fontWeight: "bold",
                        color: "white", 
                        border: '6px solid black',
                        borderRadius: '4px', 
                        backgroundColor:'black'}}
                        className="link dark-gray f6 f5-l dib mr3 mr4-l"
                    
                        title="Home"
                        >
                        Home
                        </NavLink>
                        <NavLink exact to= {'/aboutUs'} exact={true} activeClassName='is-active' activeStyle={{
                        fontWeight: "bold",
                        color: "white", 
                        border: '6px solid black',
                        borderRadius: '4px', 
                        backgroundColor:'black'}}
                        className=" link  dark-gray f6 f5-l dib mr3 mr4-l"
                    
                        title="About us"
                        >
                        About Us
                        </NavLink>
                        <NavLink exact to= {'/administration'} exact={true} activeClassName='is-active' activeStyle={{
                        fontWeight: "bold",
                        color: "white", 
                        border: '6px solid black',
                        borderRadius: '4px', 
                        backgroundColor:'black'}}
                        className="link dark-gray f6 f5-l dib mr3 mr4-l"
                    
                        title="Administration"
                        >
                        Administration
                        </NavLink>
                        <NavLink exact to= {'/departments'} exact={true} activeClassName='is-active' activeStyle={{
                        fontWeight: "bold",
                        color: "white", 
                        border: '6px solid black',
                        borderRadius: '4px', 
                        backgroundColor:'black'}}
                        className="link dark-gray f6 f5-l dib mr3 mr4-l"
                    
                        title="Departments"
                        >
                        Departments
                        </NavLink>
                        <NavLink exact to= {'/downloads'} exact={true} activeClassName='is-active' activeStyle={{
                        fontWeight: "bold",
                        color: "white", 
                        border: '6px solid black',
                        borderRadius: '4px', 
                        backgroundColor:'black'}}
                        className="link  dark-gray f6 f5-l dib mr3 mr4-l" 
                        
                        title="Downloads">
                        Downloads
                        </NavLink>
                        <NavLink exact to= {'/gallery'} exact={true} activeClassName='is-active' activeStyle={{
                        fontWeight: "bold",
                        color: "white", 
                        border: '6px solid black',
                        borderRadius: '4px', 
                        backgroundColor:'black'}}
                        className="link  dark-gray f6 f5-l dib mr3 mr4-l" 
                        
                        title="Gallery">
                        Gallery
                        </NavLink>
                    </div>
                </nav>

            </div>
        )
    }
}

export default Navbar;
