import React, { Component } from 'react'
import SlideShow from './MyCallender'
import MyCallender from './MyCallender'

export default class Home extends Component {
    render() {
        return (
            <div>
                <header className="sans-serif">
                <div
                    className="cover bg-left bg-center-l"
                    style={{ backgroundImage: "url(https://www.newtimes.co.rw/sites/default/files/styles/mystyle/public/main/articles/2017/08/11/150248775614744947291---Copy.jpg)" }}
                >
                    <div className="bg-black-70 pb5 pb6-m pb5-l shadow-3">
                    <nav className="dt w-100 mw8 center">
                    </nav>
                    <div className="tc tc-l mt0 mt1-m mt0-l  ph3">
                        <img className='' src= {require ('../images/logo512.png')} style={{height:'280px', width:'240px'}} />
                        <h2 className="georgia fw1 f2 white-80  mt3 mb4">Mission to unreached areas church vocational training center</h2>
                        <h1 className="f2 f1-l fw2 white-90 georgia mb0 lh-title">
                        M.U.A.C - VTC
                        </h1>
                    </div>
                    </div>
                </div>
                </header>
                <article className="center mw7 pa2 ma2 mw9-ns br3 hidden ba b--black-10 mv4 shadow-2 ">

                <article className="pa4-ns">
                    <div className="fl w-100 w-70-ns ">
                        <article className="athelas ml0 mt0 pl4 pr4 black-90 ">
                            <h1 className=' pl2'>DIRECTOR'S MESSAGE <br></br>
                            <div className='i f7 underline'> May 12, 2020 by MUAC-VTC</div></h1>
                            
                            <img src={require('../images/director.jpg')} 
                            style={{height:'300px', width:'420px', left:'2px'}} 
                            className='pl2'/>
                        </article>
                        
                        <div className="pa4">
                            <blockquote className="athelas ml0 mt0 pl3 pr3 black-90 bl bw2 b--blue"> 
                                <p className=' i f6'>Greetings from Mission to unreached areas church vocational training center (MUAC-VTC)</p>
                                <p className="f6 f6-m f6-l lh-copy mt0 tj">
                                The Mission to unreached areas church vocational training center is embedded with the most congenial atmosphere and boasts the students to excel in their academic pursuit. 
                                We are committed to provide facilities of the highest standard to kindle unfettered spirit of knowledge that broadens the mindsets and creates positive attitudes. 
                                Our educational experience further reiterates our stand for quality and intellectual stimulation.
                                We inculcate moral, spiritual and ethical values in a student’s personality, enriched with academic and administrative skills. 
                                This in turn instills the confidence to care for their families and the society at large. We provide a roadmap for development with fortitude to produce high level human resources and widen the domain of learning.
                                </p>
                                <p className="f6 f6-m f6-l lh-copy mt0 tj"> 
                                By and large, our system of education has a global outlook and international exposure, produces a new breed of youths with practical and administrative skills needed for national development.
                                <p>May God bless you.</p>
                                </p>
                                <cite className="f6 ttu tracked fs-normal">―lidya Meena</cite>
                            </blockquote>
                        </div>
                    </div>
                    <div className="fl w-100 w-30-ns pv5-ns tc">
                        <section className="tc mw7 pa2 shadow-1 ma2">
                            <article className="mw7 center">
                                <button style={{
                                fontWeight: "bold",
                                color: "white", 
                                border: '6px solid black',
                                borderRadius: '4px', 
                                backgroundColor:'black'}}>CALLENDER</button>
                                <br></br>
                                <MyCallender />
                            </article>
                        </section>
                        <br></br>
                        <br></br>
                        <br></br>
                        <section className=" tc mw7 pa2 shadow-1 ma2">
                        <div className="iframe-container">
                            <iframe
                                width={360}
                                height={200}
                                src="https://www.youtube.com/embed/PmVb3J0XptI"
                                frameBorder={0}
                                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                                allowFullScreen>
                            </iframe>
                        </div>
                        </section>
                    </div>
                </article>
                <article className="cf pa4-ns">
                    <div className="fl w-100 w-80-ns pa2 bg-white tc">
                        <button style={{
                        fontWeight: "bold",
                        color: "white", 
                        border: '6px solid black',
                        borderRadius: '4px', 
                        backgroundColor:'black'}}>WHAT WE OFFER</button>
                        <article className="cf">
                            <div className="fl w-100 w-50-ns  tc">
                                <article className="center mw7 pa2 ma2 mw6-ns br3 hidden ba b--black-10 mv4 shadow-1 ">
                                    <img
                                        src= {require("../images/home1.JPG")}
                                        className="db w-100 br2 br--top"
                                        alt="Photo of a kitten looking menacing."
                                    />
                                    <h1 className="f4  br3 br--top black-60 mv0 pv2 ph3">
                                        Individual Attention
                                    </h1>
                                    <div className="pa3 bt b--black-10 tj">
                                        <p className="f6 f5-ns lh-copy measure">
                                        The classroom environment at MUAC-VTC allow teachers to 
                                        give students time and attention that they need in order to succeed. 
                                        We believe in keeping class numbers low to maximum learning potential.
                                        </p>
                                    </div>
                                </article>

                            </div>
                            <div className="fl w-100 w-50-ns  tc">
                                <article className="center mw7 pa2 ma2 mw6-ns br3 hidden ba b--black-10 mv4 shadow-1 ">
                                    <img
                                        src= {require("../images/home2.JPG")}
                                        className="db w-100 br2 br--top"
                                        alt="Photo of a kitten looking menacing."
                                    />
                                    <h1 className="f4  br3 br--top black-60 mv0 pv2 ph3">
                                        Passionate trainers
                                    </h1>
                                    <div className="pa3 bt b--black-10 tj">
                                        <p className="f6 f5-ns lh-copy measure">
                                        At MUAC-VTC we boast having the best teachers in every programme that we offer.
                                        There is conducive enviroment and relationship between teachers and students inside class and outside 
                                        of the class as well.
                                        </p>
                                    </div>
                                </article>

                            </div>
                            <div className="fl w-100 w-50-ns  tc">
                                <article className="center mw7 pa2 ma2 mw6-ns br3 hidden ba b--black-10 mv4 shadow-1 ">
                                    <img
                                        src= {require("../images/home3.JPG")}
                                        className="db w-100 br2 br--top"
                                        alt="Photo of a kitten looking menacing."
                                    />
                                    <h1 className="f4  br3 br--top black-60 mv0 pv2 ph3">
                                        Life Long Learning
                                    </h1>
                                    <div className="pa3 bt b--black-10 tj">
                                        <p className="f6 f5-ns lh-copy measure">
                                        We believe in equipping students with the skills to be able to learn 
                                        for the rest of their lives! By instilling in them a method of analytical thinking, 
                                        we believe they will be able to be independent and high performers in their chosen careers.
                                        </p>
                                    </div>
                                </article>

                            </div>
                            <div className="fl w-100 w-50-ns  tc">
                                <article className="center mw7 pa2 ma2 mw6-ns br3 hidden ba b--black-10 mv4 shadow-1 ">
                                    <img
                                        src= {require("../images/home2.JPG")}
                                        className="db w-100 br2 br--top"
                                        alt="Photo of a kitten looking menacing."
                                    />
                                    <h1 className="f4  br3 br--top black-60 mv0 pv2 ph3">
                                        Fantastic Facilities
                                    </h1>
                                    <div className="pa3 bt b--black-10 tj">
                                        <p className="f6 f5-ns lh-copy measure">
                                        At MUAC-VTC there are state of the art facilities and Each student has access to the best possible learning technologies. 
                                        We believe this better prepares students for their careers or own businesses.
                                        </p>
                                    </div>
                                </article>

                            </div>
                            
                        </article>

                    </div>
                    <div className="fl w-100 w-20-ns bg-white tj tc pa2">
                        <button style={{
                        fontWeight: "bold",
                        color: "white", 
                        border: '6px solid black',
                        borderRadius: '4px', 
                        backgroundColor:'black'}}>Anouncements</button>
                        <p className="f6 f5-ns lh-copy measure">
                        No New Announcements available!
                        </p>
                    </div>
                </article>
                </article>

            </div>
        )
    }
}
